// MOBILE
@media only screen and (max-width: 480px), only screen and (max-device-width: 480px) {
	.main {
		.button-container {
			position: absolute;
			top: 70%;
			display: inline;
		}

		.scroll-notice {
			width: 100%;
			position: absolute;
			left: 0;
			top: 96%;
			font-size: 15pt;
	
			.fa-solid {
				font-size: 15pt;
				vertical-align: middle;
				margin-right: 7px;
			}
		}
	}

	.button {
		min-width: 75px;
		padding: 12px 12px !important;
		font-size: 15px !important;
		margin: 5px;
		
		.fa-brands, .fa-solid {
			margin-right: 5px !important;
			font-size: 17px;
		}
	}

	.section {
		flex-flow: column;
		display: flex;
		padding: 20px 10px;
		min-height: 100px;
		max-width: 95%;
	
		.center,
		.left,
		.right {
			text-align: center;
			margin: 0 10px;
		}
	
		img {
			width: auto; /* Allow the width to adjust based on the image's aspect ratio */
			height: auto; /* Allow the height to adjust based on the image's aspect ratio */
			max-width: 100%; /* Ensure the image doesn't exceed the width of its container */
			max-height: 100%; /* Ensure the image doesn't exceed the height of its container */
			object-fit: contain; /* Ensure the image maintains its aspect ratio while fitting within its container */
			display: block; /* Ensure the image is displayed as a block element */
			border-radius: 10px;
		}
	
		h2 {
			font-size: 32px;
		}
	
		p {
			font-size: 14px;
		}
	
		.small {
			font-size: 10px;
		}
	}
}