// LAYOUT -- Every rules that relates to the general layout of the website goes here

.main {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100vh;
	position: relative;
	overflow: hidden;
	background-size: cover;
	background-position: top right;
	background-attachment: fixed;

	.button-container {
		position: absolute;
		top: 80%;
		display: inline-flex;
	}

	.scroll-notice {
		width: 100%;
		position: absolute;
		left: 0;
		top: 95%;
		font-size: 18pt;

		.fa-solid {
			font-size: 20pt;
			vertical-align: middle;
			margin: 0px 10px !important;
		}
	}
}

.main .title {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	color: #fff;
	text-align: center;
	text-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);
}

.page {
	background-image: url("./img/HellBG.jpg");
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	background-position: center;

	.has-webp & {
		background-image: url("./img/webp/HellBG.webp");
	}
}

.info-stuff {
	padding-top: 50px;
	
	background-position: relative;
	background-image: url("./hellish-pattern.png");
	background-repeat: repeat;
	background-size: cover;

	-webkit-mask-image: linear-gradient(to bottom,
		rgba(0, 0, 0, 0) 0%,
		rgb(14, 4, 4, 1) 100px);
	mask-image: linear-gradient(to bottom,
		rgba(0, 0, 0, 0) 0%,
		rgb(14, 4, 4, 1) 100px);
}

.back-overlay {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: linear-gradient(to bottom,
			rgba(0, 0, 0, 0) 0%,
			rgba(0, 0, 0, 0.75) 100%);
	backdrop-filter: contrast(1.25);
}

.section {
	width: 100%;
	overflow: hidden;
	margin: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	padding: 50px;
	color: #fff;
	max-width: 1300px;

	.center,
	.left,
	.right {
		display: inline-block;
		margin: 0 30px;
	}

	.image {
		flex: 1;
		position: relative;
		height: 100%;
		display: flex;
	}

	.left {
		text-align: right;
	}

	.image.left {
		justify-content: flex-end;
	}

	.image.right {
		justify-content: flex-start;
	}

	img {
		border-radius: 15px;
	}

	.center {
		width: 100%;
		text-align: justify;
		justify-content: space-evenly;
	}

	h2 {
		font-family: "Fronds Getturing", sans-serif;
		z-index: 10;
		font-size: 42px;
		margin: 5px 0;
	}

	.justify {
		text-align: justify;
	}

	p {
		font-family: "Tsukimi Rounded Regular", sans-serif;
		z-index: 10;
		font-size: 24px;
		line-height: 1.4;
		margin: 24px 0 12px;

		b {
			font-family: "Tsukimi Rounded Medium", sans-serif;
		}
	}

	.small {
		font-family: "Tsukimi Rounded Light", sans-serif;
		font-size: 14px;
		display: block;
		opacity: 0.65;

		b {
			font-family: "Tsukimi Rounded Regular", sans-serif;
		}
	}
}

.newsletter {
	position: relative;
	overflow: hidden;
	max-height: auto; /* Set maximum height to 50vh */
	max-width: 1100px;

	img {
		width: auto; /* Allow the width to adjust based on the image's aspect ratio */
		height: auto; /* Allow the height to adjust based on the image's aspect ratio */
		max-width: 100%; /* Ensure the image doesn't exceed the width of its container */
		max-height: 100%; /* Ensure the image doesn't exceed the height of its container */
		object-fit: contain; /* Ensure the image maintains its aspect ratio while fitting within its container */
		display: block; /* Ensure the image is displayed as a block element */
	}

	.box {
		position: absolute;
		text-align: center;
		padding: 9% 20% 20% 33%; // top, right, bottom, left

		h2 {
			font-size: 2.8vw;
		}

		p {
			font-size: 1.3vw;
		}
		
		.small {
			font-size: .9rem;
		}
	}

	.form-control {
		position: relative;
		margin: 30px 0 20px;
	}

	.input {
		border: 0;
		border-radius: 50px;
		box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
		display: block;
		font-family: "Muli", sans-serif;
		font-size: 1rem;
		height: 3rem;
		padding: 12px 25px;
		width: 100%;
	}
}